*{
  padding: 0;
  margin: 0;
}
body {
  margin: 0;
  padding:0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.Navbar{
  display: flex;
 position: fixed;
  background-color: black;
  z-index: 5;
  width: 100vw;
  color:white;
  justify-content: space-around;
  height: fit-content;
  padding: 10px;
}
.logo{
  font-family: 'Mate SC', serif;
  font-size: 30px;
  font-weight: 600;
  color: #F40612;
}

.body{
  background-color: #414141;
  padding-top: 40px;
}

.input{
  background-color: white;
}
.form input{
  padding-left: 4px;
  font-size: 15px;
  padding: 10px;
  width:30vw;
  border:none;

}
.form input:active{
  border:none;
  
}

.poster-container{

 
}
.poster{
  margin-top:20px ;
  /* background-image: url('./backdrop.jpg'); */
  height: 400px;
  background-position: right;
  
  background-size:58%;
  background-repeat: no-repeat;
  background-position-x: 600px;
}
.poster .poster-detail{
  color: white;
  width: 80%;
  height: 100%;
  background-image: linear-gradient(to right,rgb(0, 0, 0),rgba(255,0,0,0));
}
.poster .poster-detail .p-detail{
  display: flex;
  flex-direction: column;

  padding-top:70px;
  padding-left: 40px;
  text-align: center;
  width: 45%;
  justify-content: space-between;
  height: auto;
}

.poster .poster-detail .p-detail .movie-title{
  margin-bottom: 50px;
  font-weight: 800;
  font-size: 50px;
}
.poster .poster-detail .p-detail .movie-detail{
  margin-top: 15px;
}
.search-btn{
  color:white;
  font-size: 15px;
  padding: 10px;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
  border: none;
  background-color: #F40612;
}
.main{
  background-color: #323232;
}

.link{
  text-decoration: none;
}

.movie-box{
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-end;
  height:300px;
  width: 202px;
  background-image: url('./poster.jpg');
  background-size: cover;

}
.container-title{
font-size: 40px;
color: white;
font-family: 'Baloo Tammudu 2', cursive;

}
.box{
  display: flex;
  justify-content: space-between;
  /* margin: 20px; */
  margin-left: 30px;
  margin-right: 30px;
}

.container{
  padding: 30px;
}
.movie-details{
  position: relative;
  padding: 3px;
  width: 100%;
  height: 130px;
  background-image: linear-gradient(rgba(255,0,0,0),rgb(0, 0, 0));
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 5px;
  color: white;
}
.movie-details .title{
  
  font-size: 30px;
  font-weight: 500;
}
.line{
  text-decoration: line-through;
}
.detail{
  display: flex;
  justify-content: space-between;

  font-size: 13px;
  color: rgb(114, 114, 114);
}
.detail span{
  width: 22%;
  text-align: center;
  border-right: 1px solid rgb(114, 114, 114);
}
.detail .last{
  border-right:none;
}
.container .MuiIconButton-root{
  background-color: black;
  opacity: 0.6;
  color: white;
}
.container .MuiIconButton-root:hover{
  color: black;
  opacity: 1;
}


.left{
  z-index: 1;
  top:175px;
  background-color: grey;
  position: absolute;
}
.right{
  float: right;
  z-index: 1;
  top:175px;
  background-color: grey;
  position: absolute;
}

.footer{
  background-color: black;
  color: white;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}
.footer div{
  width: 100%;
  text-align: center;
}
ul{
  list-style: none;
}
.footer-about .title{
  font-family: 'Mate SC', serif;
  font-size: 30px;
  font-weight: 600;
  color: #F40612;
}
.toshow{
  width: 50%;
  display: flex;
  justify-content: space-between;
}
.footer-category .title{
  font-weight: 600;
  font-size: 28px;
}
.footer-social {
  display:flex;
  flex-direction: column;
  justify-content: center;
}
.footer-social .MuiIconButton-root{
  color: white;
}
.details{
  background-color: #181818;
  color: white;
}
.details-top{
  background-image: url('./backdrop.jpg');
  height: 100vh;
  background-size: cover;
}
.details-data{
 color: white;
 padding-left: auto;
 padding-top: auto;
 height: 100%;
 padding: 20px;
 background-image: linear-gradient(to right,rgb(0, 0, 0),rgba(255,0,0,0));
 width: 40%;
 justify-content: center;
}

.details-data .detail{
   width: 70%;
   font-size: 16px;
   margin-top: 5px;
}
.details-data .title{
  text-transform: uppercase;
  font-size: 80px;
  font-weight: 800;
  letter-spacing: -5px;
  font-family: 'Roboto', sans-serif;
  margin-bottom: 50px;
}
.details-data .type{
  font-size: 30px;
  font-weight: 500;
}
.details-data .about{
  padding: 10px;
}
.details-data .star{
 padding: 10px;
}
.details-data .creator{
 padding: 10px;
}
.details-data .star span{
  color: rgb(114, 114, 114);
}
.details-data .creator span{
  color: rgb(114, 114, 114);
}
.details-data .detail-button{
  margin: 10px;
  background-color: red;
  color: white;
}
.trailer{
  padding: 40px;
}
.trailer .trailer-title{
  font-size: 50px;
  font-weight: 500;
  margin-bottom: 30px;
}
.trailer .videos{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.details-bottom{
  padding: 40px;
}
.details-bottom .title{
  font-size: 50px;
  font-weight: 500;
  margin-bottom: 30px;
}
.movie-card{
  background-image: url('./backdrop.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  height:200px;
  width: 400px;
  flex-wrap: wrap;
  margin-top: 50px;
  display: flex;
  flex-direction: column-reverse;
}
.movie-card-title{
  font-size: 30px;
  font-weight: 600;
  background-color: black;
  opacity: 0.6;
  padding: 5px;
  padding-left: 10px;
}
.details-video{
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
 justify-content: space-between;
}
.details-video .video-card{
   margin: 10px;
}
.search {
padding: 80px;
}
.search .search-title{
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 30px;
  color: white;
}
.search-card{
  height: 300px;
  margin-top: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url('./backdrop.jpg');
}
.search-card .search-card-details{
  background-image: linear-gradient(to right,rgba(255,0,0,0),rgb(0, 0, 0));
  height: 100%;
  overflow-y: scroll;
}
.search-detail{
 margin-top: 20px;
 margin-right: 20px;
  color: white;
  width:65vw;
  float: right;
}
.search-card-title{
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 20px;
  text-transform:uppercase;
}
.search-card-release-date{
  font-size: 30px;
  font-weight: 400;
  margin-bottom: 15px;
}
.movie-detail{
  margin-top: 10px;
}
.search-icon{
  
}

